import React from 'react'
import PageContainer from '../../components/layout/PageContainer'
import NotInterestedIcon from '@mui/icons-material/NotInterested';
import { Typography } from '@mui/material';
import PrimaryBtnMD from '../../components/UI/Atoms/Button/PrimaryBtnMD';
import { useNavigate } from 'react-router-dom';

const CantAccessPage = () => {
  const navigate = useNavigate();


  return (
    <PageContainer>
      <div className="mt-[80px] mb-[20px]">
        <NotInterestedIcon className="w-[100px] h-[100px] text-[#5E81F4]" />
      </div>
      <Typography variant="h2" className="mb-[30px] tracking-normal">
        현재 페이지에 접근 권한이 없습니다.
      </Typography>
      <Typography className="text-center tracking-normal leading-7 mb-[40px]">
        방문하시려는 페이지는 권한이 있는 회원만 접근할 수 있습니다.
        <br />
        로그인 정보를 다시 확인해 주세요.
      </Typography>
      <div className="flex gap-[15px]">
        <PrimaryBtnMD onClick={() => navigate('/')} className="bg-[#ffffff] text-[#5E81F4] hover:bg-[#f5f7f8]">
          홈으로
        </PrimaryBtnMD>
        <PrimaryBtnMD onClick={() => navigate('/account/login/association')}>
          협회 로그인
        </PrimaryBtnMD>
      </div>
    </PageContainer>
  );
}

export default CantAccessPage
